<template>
    <div>
        <div v-for="(item, index) in getGamesList" v-bind:key="index">  
            <div v-if="search_v.length > 3">
                <div class="gamesBox" v-bind:style="{ 'background-color': item.color }" v-if="item.id == search_v">
                    <h4>{{ item.id }}</h4>
                    <h4>{{ item.nombre }}</h4>
                    <h4>Stock: {{ item.stock }}</h4>
                    <h4>$ {{ item.precio }}</h4>
                </div>
            </div>
            <div v-else>
                <div class="gamesBox" v-bind:style="{ 'background-color': item.color }">
                    <h4>{{ item.id }}</h4>
                    <h4>{{ item.nombre }}</h4>
                    <h4>Stock: {{ item.stock }}</h4>
                    <h4>$ {{ item.precio }}</h4>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default 
{
    props: 
    { 
        search_v: String
    },
    computed: 
    {
        getGamesList() 
        { 
            return this.$store.getters.getterGamesList
        }
    }
}
</script>
<style scoped>
    .gamesBox
    { 
        display: flex;
        flex-direction: row; 
    }
    h3
    {
        padding: 8px;
    }
    h4
    {
        padding: 8px;
        width: 192px;
    }
</style>
