<template>
    <div>
        <div class="searchBox">
            <p>Filtra por codigo:</p>
            <input type="text" v-model="searchInput">
        </div>
        <h3>Cantidad de juegos totales: {{ getTotalGames }}</h3>
        <h3>Cantidad de stock total: {{ getTotalStock }}</h3> 
        <Listado v-bind:search_v="searchInput"></Listado>
    </div>
</template>
<script>

import Listado from '../components/Listado'

export default 
{
    data() 
    {
        return {
            searchInput: ""
        } 
    },
    components:
    {
        Listado
    },
    computed: 
    {
        getTotalGames()
        { 
            return this.$store.getters.getterTotalGames
        },
        getTotalStock() 
        { 
            return this.$store.getters.getterTotalStock
        }
    }
}
</script>
<style scoped>
    .gamesBox
    { 
        display: flex;
        flex-direction: row; 
    }
    h3
    {
        padding: 8px;
    }
    h4
    {
        padding: 8px;
        width: 192px;
    } 
</style>
